.exampleGrid {
  display: grid;
  width: 100%;
  height: 700px;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  border: 1px solid white; }
  .exampleGrid > div {
    background: #ff3f3f;
    width: 100%;
    height: 100%; }
    .exampleGrid > div.blueBox {
      grid-row: 3 / 4;
      grid-column: 1 / 2;
      background: #4b4bf3; }
    .exampleGrid > div:last-of-type {
      background: #67fd7d; }

.gridContainer {
  margin-top: 64px;
  display: grid;
  width: 100%;
  height: 500px;
  grid-gap: 16px;
  grid-template-rows: 0.3fr 1fr 0.3fr;
  grid-template-columns: 0.3fr 1fr 0.3fr;
  grid-template-areas: "nav header header" "nav main aside" "nav footer footer";
  -webkit-box-shadow: 10px 10px 90px rgba(0, 0, 0, 0.19);
          box-shadow: 10px 10px 90px rgba(0, 0, 0, 0.19); }
  .gridContainer header {
    grid-area: header; }
  .gridContainer nav {
    grid-area: nav;
    background-color: #6f6f6f; }
  .gridContainer footer {
    grid-area: footer; }
  .gridContainer aside {
    grid-area: aside;
    background: #6f6f6f; }
  .gridContainer main {
    grid-area: main;
    background-color: #949494;
    text-align: left;
    padding: 16px; }
  .gridContainer header,
  .gridContainer footer {
    background-color: #fff;
    color: #000; }

@media (max-width: 800px) {
  .gridContainer {
    grid-template-columns: 0.5fr 0.5fr;
    grid-template-rows: 0.3fr 1fr 0.3fr;
    grid-template-areas: "nav nav" "main aside" "header footer"; }
    .gridContainer nav {
      background: white;
      color: #000; }
    .gridContainer header {
      background-color: #6f6f6f;
      color: white; }
    .gridContainer main {
      text-align: center; } }
@media (max-width: 500px) {
  .gridContainer {
    grid-template-columns: 1fr;
    grid-template-areas: "header" "main" "footer"; }
    .gridContainer aside,
    .gridContainer nav {
      display: none; }
    .gridContainer nav {
      background: white;
      color: #000; }
    .gridContainer header {
      background-color: #6f6f6f;
      color: white; }
    .gridContainer main {
      text-align: center; } }
body {
  margin: 0;
  padding: 0; }

@font-face {
  font-family: "Circular Air";
  src: url("./fonts/CircularAir.otf") format("opentype"); }



